import {
  browserLocalPersistence,
  browserSessionPersistence,
  createUserWithEmailAndPassword,
  GoogleAuthProvider,
  sendEmailVerification,
  sendPasswordResetEmail,
  setPersistence,
  signInWithEmailAndPassword,
  signInWithPopup,
  updateProfile,
} from "firebase/auth"

import ForgotPassowrd from "@/types/auth/ForgotRequest"
import LoginRequest from "@/types/auth/LoginRequest"
import RegisterRequest from "@/types/auth/RegisterRequest"

import { auth } from "./firebase.config"

const REDIRECT_URL = "http://localhost:4000"
const PASSWORD_RESET_URL = `http://localhost:4000/reset-password`

export async function loginWithEmail(req: LoginRequest) {
  try {
    const { email, password, rememberMe } = req
    await setPersistence(
      auth,
      rememberMe ? browserLocalPersistence : browserSessionPersistence
    )

    const userCredential = await signInWithEmailAndPassword(
      auth,
      email,
      password
    )
    const user = userCredential.user
    const idToken = await user.getIdToken()
    return { user, idToken }
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
  } catch (e: any) {
    if (
      e.code === "auth/invalid-credential" ||
      e.code === "auth/wrong-password"
    ) {
      throw {
        message: "Invalid email or password",
      }
    }
    if (e.code === "auth/user-not-found") {
      throw { message: "User not found" }
    }

    throw { message: "Failed to login. Please try again" }
  }
}

export async function loginWithGoogle(rememberMe: boolean = false) {
  try {
    await setPersistence(
      auth,
      rememberMe ? browserLocalPersistence : browserSessionPersistence
    )
    const result = await signInWithPopup(auth, new GoogleAuthProvider())
    const user = result.user
    const idToken = await user.getIdToken()

    return { user, idToken }
  } catch (e) {
    if (process.env.NODE_ENV === "development") console.error(e)
    throw { message: "Google login failed. Please try again later." }
  }
}

export async function registerWithFirebase(req: RegisterRequest) {
  try {
    const userCredential = await createUserWithEmailAndPassword(
      auth,
      req.email,
      req.password
    )
    const user = userCredential.user

    if (req.username) {
      await updateProfile(user, {
        displayName: req.username,
      })
    }

    await sendEmailVerification(user, {
      url: REDIRECT_URL,
    })

    return {
      success: true,
      user: {
        uid: user.uid,
        email: user.email,
        displayName: user.displayName,
        emailVerified: user.emailVerified,
      },
    }
  } catch (error: any) {
    console.error("Error registering user:", error)

    let errorMessage = "Register failed. Please try again later."

    switch (error.code) {
      case "auth/email-already-in-use":
        errorMessage = "Email is already in use."
        break
      case "auth/weak-password":
        errorMessage = "Weak password."
        break
      case "auth/invalid-email":
        errorMessage = "Invalid email."
        break
      case "auth/operation-not-allowed":
        errorMessage = "Login is not availabel."
        break
      default:
        errorMessage = error.message
    }

    throw {
      success: false,
      message: errorMessage,
    }
  }
}

export async function forgotPassword({ email }: ForgotPassowrd) {
  try {
    const actionCodeSettings = PASSWORD_RESET_URL
      ? {
          url: PASSWORD_RESET_URL,
          handleCodeInApp: true,
        }
      : undefined

    await sendPasswordResetEmail(auth, email, actionCodeSettings)

    return {
      success: true,
      message: "Email has been sent. Please check your mailbox.",
    }
  } catch (error: any) {
    console.error("Error sending password reset email:", error)

    let errorMessage = "Email sent failed."

    switch (error.code) {
      case "auth/user-not-found":
        errorMessage = "User not found."
        break
      case "auth/invalid-email":
        errorMessage = "Invalid email."
        break
      case "auth/too-many-requests":
        errorMessage = "Too many request. Please try again later."
        break
      case "auth/network-request-failed":
        errorMessage = "Network connection failed."
        break
      default:
        errorMessage = error.message
    }

    return {
      success: false,
      message: errorMessage,
    }
  }
}
